.home-page {
  text-align: center;
  margin-top: 100px;
  color: #333;
}

.home-page h2 {
  font-size: 2.5rem;
  color: #1e1e2f;
}

.home-page p {
  font-size: 1.2rem;
  margin: 20px 0;
  color: #36395f;
}

.button-container {
  margin-top: 30px;
}

.button-container button {
  margin: 0 10px;
  padding: 10px 20px;
  background-color: hsl(193, 40%, 24%);
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.button-container button:hover {
  background-color: #435960;
}
.home-page {
  text-align: center;
  padding: 3rem;
  font-family: 'Segoe UI', sans-serif;
  background-color: #f4f6fa;
}

.hero h1 {
  font-size: 2.5rem;
  color: #1e2a38;
}

.hero p {
  font-size: 1.2rem;
  margin-top: 0.5rem;
  color: #555;
}

.cta-buttons {
  margin-top: 1.5rem;
}

.primary,
.secondary {
  padding: 0.75rem 1.5rem;
  margin: 0 0.5rem;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
}

.primary {
  background-color: #007bff;
  color: white;
}

.secondary {
  background-color: #e0e0e0;
  color: #333;
}

.features {
  display: flex;
  justify-content: space-around;
  margin-top: 3rem;
}

.feature-card {
  background: white;
  padding: 2rem;
  border-radius: 8px;
  width: 25%;
  box-shadow: 0 4px 8px rgba(0,0,0,0.05);
}